body {
  background: #fffc00;
  background: radial-gradient(circle, #fffc00 3%, #ff7a00 69%, #ab5200 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  font-family: "Century Gothic", "Arial"; }

.side-ads {
  margin-left: 200px !important;
  margin-right: 200px !important; }

.no-url-underline {
  text-decoration: none; }

.half-page {
  margin-left: 35px;
  width: 45%; }

.bubble-button {
  font-family: "Century Gothic", "Arial";
  font-display: fallback;
  font-weight: 300;
  font-size: 10px;
  opacity: 0;
  animation: initial-slide-right 0.5s forwards 1s ease-in-out; }
  .bubble-button a {
    text-decoration: none; }
  .bubble-button #COLLABORATE {
    color: #2c1818;
    font-weight: bold;
    background: #fffc00;
    background: linear-gradient(35deg, #fffc00 7%, #ffc900 91%);
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 25px;
    padding-right: 25px;
    border-radius: 30px;
    margin-left: 72%; }

.bubble-social {
  background-color: white;
  border-radius: 50%;
  width: 60px;
  height: 60px; }
  .bubble-social a {
    transition: transform 0.15s forease-in-out; }
  .bubble-social img {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    width: 40px;
    height: 40px; }
  .bubble-social:hover {
    transform: scale(1.1); }

.header {
  font-family: "Century Gothic", "Arial";
  font-display: block;
  color: white;
  margin: 25px 25px 60px; }
  .header #KVIN {
    font-size: 30.5px;
    font-weight: 300; }
  .header #TANAKA {
    font-size: 30.5px;
    font-weight: 600; }
  .header #SOFTWARE-DEVELOPER {
    font-size: 10px;
    letter-spacing: 1px;
    padding-left: 2px; }
  .header nav {
    position: absolute;
    left: 30%;
    top: 45px; }
  .header a {
    text-decoration: none;
    font-weight: bold;
    font-size: 11px;
    color: white;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 24px;
    padding-right: 24px;
    border-radius: 40px;
    border: 2px solid white;
    margin-left: 20px;
    margin-right: 20px; }
    .header a #HOME {
      animation: intial-appearance 0.5s forwards ease-in-out; }
    .header a #CURRENT-PROJECTS {
      display: none; }
    .header a #ARTICLES {
      display: none; }

.quote {
  font-family: "Georgia", "Times New Roman";
  font-display: fallback;
  text-align: left;
  color: black;
  margin: 27px 15px 15px 15px; }
  .quote #SMALL {
    font-size: 14px;
    padding-bottom: 20px;
    line-height: 1.2;
    opacity: 0;
    animation: initial-appearance 0.7s forwards 0.5s ease-in-out; }
  .quote #BIG {
    font-size: 19px;
    letter-spacing: 0.5px;
    padding-bottom: 10px;
    line-height: 1.2;
    opacity: 0;
    animation: initial-appearance 0.7s forwards 1s ease-in-out; }

.current-projects {
  font-family: "Century Gothic", "Arial"; }
  .current-projects a {
    padding-left: 20px; }

.align-middle-each {
  font-family: "Century Gothic", "Arial"; }
  .align-middle-each span {
    vertical-align: middle; }
    .align-middle-each span a {
      text-decoration: none;
      margin-left: 20px; }
  .align-middle-each img {
    vertical-align: middle;
    width: 50px;
    height: 50px; }

.socials {
  margin-left: 40px;
  margin-top: 70px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: row; }

.react-logo {
  margin-left: 20px; }
  .react-logo span {
    color: white;
    vertical-align: middle; }
  .react-logo img {
    vertical-align: middle;
    width: 50px;
    height: 50px; }

@media only screen and (max-width: 800px) {
  .header nav {
    display: none; }

  .bubble-button #COLLABORATE {
    display: none; }

  .half-page {
    width: auto;
    margin-right: 35px;
    margin-left: 35px; } }
@keyframes initial-slide-up {
  from {
    transform: translateY(30%);
    opacity: 0; }
  to {
    transform: translateY(0);
    opacity: 1; } }
@keyframes initial-slide-right {
  from {
    transform: translateX(-10%);
    opacity: 0; }
  to {
    transform: translateX(0);
    opacity: 1; } }
@keyframes initial-appearance {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
